import React, { Component } from 'react';
import corazon from '../../../assets/icons/corazon.svg'
import coin from '../../../assets/icons/coin.png'
import './wellcome.css'
class Wellcome extends Component {
    constructor(props) {
        super(props);
        this.state = {  }
    }
    render() { 
        return ( 
            <div className = " wellcome-div">
                 <h1 className="title"> All Courses</h1>
                <p>Filter By</p>
                <div classname="filter-div">
                <div className="level-div">
                    <h2> <img className="corazon" src={corazon} /> Level</h2>
                    <button>Easy</button>
                    <button>Medium</button>
                    <button>Hard</button>
                </div>
                <div className="price-div">
                    <h2>Pice  <img className="coin" src={coin} /> </h2>
                    <button>Free</button>
                    <button>Paid</button>
                </div>

                </div>
             
                
            </div>
         );
    }
}
 
export default Wellcome;